html, body {
    width: 100%;
    font-family: 'Avenir', Helvetica, Arial, sans-serif, "微软雅黑", "宋体";
    /* -webkit-font-smoothing: antialiased;
     -moz-osx-font-smoothing: grayscale;*/
}

a, input, button, svg, div {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

input, button {
    -webkit-appearance: none;
    border-radius: 0;
    outline: none;
}

h1, h2, h3, h4 {
    font-weight: normal;
}

*, *:before, *:after {
    box-sizing: border-box;
}


li {
    list-style: none;
}

a {
    text-decoration: none;
    cursor: pointer;
}

.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

.trans-btn {
    padding: 14px 30px;
    background-color: #005DFF;
    border-radius: 4px;
    display: inline-block;
    text-align: center;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s;
    border: none;
    & > img {
        position: relative;
        left: 0;
        transition: all 0.3s;
    }
    &:hover {
        //opacity: 0.8;
        box-shadow: 0 0.7rem 1.4rem rgb(224, 226, 232), 0 0.3rem 0.6rem rgba(12, 14, 27, 0.065);
        -webkit-transform: translateY(-.2rem);
        transform: translateY(-.2rem);
        transition-duration: .45s;
        background-color: rgb(34, 116, 255);
    }
    &:hover > img {
        left: 8px;
    }
}



